.navbarcontainer{
    background-color: rgb(60, 60, 200);
    width: 100%;
    height: 60px;
    display: flex;
    align-items: center;
}
.navelementcontainer{
    height: 40px;
    width: 240px;
    display: flex;
    align-items: center;
}
.navelementcontainer ul{
    display: flex;
    gap: 40px;
    align-items: center;
}
.navelementcontainer ul li{
    display: flex;
    align-items: center;
}
.navelementcontainer ul li a{
    display: flex;
    align-items: center;
    text-decoration: none;
    font-size: 16px;
    font-weight: 700;
    color: white;
}